import {HTTP} from '@/common/http-common'

export default {
  state: {
    verseComments: [],
    verseCommentReplies: []
  },

  getters: {
    verseComments (state) {
      return state.verseComments
    },
    verseCommentReplies (state) {
      return state.verseCommentReplies
    }
  },

  mutations: {
    verseComments (state, payload) {
      state.verseComments = payload
    },
    verseCommentReplies (state, payload) {
      state.verseCommentReplies = payload
    },
    pushNewVerseComment (state, payload) {
      // console.log('in push', payload)
      if (payload.parentCommentId) {
        state.verseCommentReplies.unshift(payload)
        for (var i = 0; i <= state.verseComments.length; i++) {
          if (state.verseComments[i].id === payload.parentCommentId) {
            state.verseComments[i].replyCount++
            break
          }
        }
      } else {
        state.verseComments.unshift(payload)
      }
    },
    removeVerseComment (state, payload) {
      console.log('in remove verse comment mutation', payload.isReply)
      if (payload.isReply) {
        var parentCommentId = 0
        for (var i = 0; i < state.verseCommentReplies.length; i++) {
          if (state.verseCommentReplies[i].id === payload.commentId) {
            parentCommentId = state.verseCommentReplies[i].parentCommentId
            state.verseCommentReplies.splice(i, 1)
            break
          }
        }
        console.log('parentCommentId', parentCommentId)
        for (i = 0; i <= state.verseComments.length; i++) {
          if (state.verseComments[i].id === parentCommentId) {
            state.verseComments[i].replyCount--
            break
          }
        }
      } else {
        for (var j = 0; j < state.verseComments.length; j++) {
          if (state.verseComments[j].id === payload.commentId) {
            state.verseComments.splice(j, 1)
            break
          }
        }
      }
    },
    toggleVerseCommentLike (state, payload) {
      console.log('toggleVerseCommentLike', payload.isReply)
      if (payload.isReply) {
        for (var i = 0; i < state.verseCommentReplies.length; i++) {
          if (state.verseCommentReplies[i].id === payload.commentId) {
            var previousIsLike = state.verseCommentReplies[i].isLike
            state.verseCommentReplies[i].isLike = payload.isLike

            if (payload.isLike === true) {
              state.verseCommentReplies[i].likeCount++
            } else if (payload.isLike === false) {
              state.verseCommentReplies[i].dislikeCount++
            }

            if (previousIsLike === true) {
              state.verseCommentReplies[i].likeCount--
            } else if (previousIsLike === false) {
              state.verseCommentReplies[i].dislikeCount--
            }

            break
          }
        }
      } else {
        for (var j = 0; j < state.verseComments.length; j++) {
          if (state.verseComments[j].id === payload.commentId) {
            previousIsLike = state.verseComments[j].isLike
            state.verseComments[j].isLike = payload.isLike

            if (payload.isLike === true) {
              state.verseComments[j].likeCount++
            } else if (payload.isLike === false) {
              state.verseComments[j].dislikeCount++
            }

            if (previousIsLike === true) {
              state.verseComments[j].likeCount--
            } else if (previousIsLike === false) {
              state.verseComments[j].dislikeCount--
            }

            break
          }
        }
      }
    }
  },

  actions: {
    fetchVerseComments ({commit, getters}) {
      console.log('online', navigator.onLine)
      if (!navigator.onLine) {
        return
      }
      commit('isLoading', true)
      HTTP.get('verses/' + getters.currentVerseId + '/comments', {params: {lang: getters.lang}}).then(response => {
        // console.log('in fetch verse comments', response.data)
        commit('verseComments', response.data)
        commit('isLoading', false)
      }).catch(e => {
        commit('error', e)
        commit('isLoading', false)
      })
    },

    fetchVerseCommentReplies ({commit, getters}, payload) {
      console.log('online', navigator.onLine)
      if (!navigator.onLine) {
        return
      }
      commit('isLoading', true)
      var endpoint = 'verses/' + getters.currentVerseId + '/comments/' + payload + '/replies'
      HTTP.get(endpoint, {params: {lang: getters.lang}}).then(response => {
        commit('verseCommentReplies', response.data)
        commit('isLoading', false)
      }).catch(e => {
        commit('error', e)
        commit('isLoading', false)
      })
    },

    saveVerseComment ({commit, getters}, payload) {
      console.log('in saveVerseComment', payload.commentText)
      return new Promise(function (resolve, reject) {
        commit('isSaving', true)
        var reqObj = {
          verseId: getters.currentVerseId,
          commentText: payload.commentText,
          lang: getters.lang,
          parentCommentId: payload.parentCommentId
        }
        var endpoint = 'verses/' + getters.currentVerseId + '/comments'
        HTTP.post(endpoint, reqObj).then(response => {
          commit('pushNewVerseComment', response.data)
          commit('isSaving', false)
          resolve()
        }).catch(e => {
          commit('error', e)
          commit('isSaving', false)
          reject(e)
        })
      })
    },

    deleteVerseComment ({commit, getters}, payload) {
      console.log('in delete verse comment action', payload.isReply)
      commit('isSaving', true)
      var endpoint = 'verses/' + getters.currentVerseId + '/comments/' + payload.commentId
      HTTP.delete(endpoint).then(response => {
        commit('removeVerseComment', {commentId: payload.commentId, isReply: payload.isReply})
        commit('isSaving', false)
      }).catch(e => {
        commit('error', e)
        commit('isSaving', false)
      })
    },

    toggleVerseCommentLike ({commit, getters}, payload) {
      commit('isSaving', true)
      var endpoint = 'verses/' + getters.currentVerseId + '/comments/' + payload.commentId + '/likes'
      if (payload.isLike === true) {
        HTTP.delete(endpoint).then(response => {
          commit('toggleVerseCommentLike', {commentId: payload.commentId, isReply: payload.isReply, isLike: null})
          commit('isSaving', false)
        }).catch(e => {
          commit('isSaving', false)
        })
      } else {
        HTTP.post(endpoint).then(response => {
          commit('toggleVerseCommentLike', {commentId: payload.commentId, isReply: payload.isReply, isLike: true})
          commit('isSaving', false)
        }).catch(e => {
          commit('isSaving', false)
        })
      }
    },

    toggleVerseCommentDislike ({commit, getters}, payload) {
      commit('isSaving', true)
      var endpoint = 'verses/' + getters.currentVerseId + '/comments/' + payload.commentId + '/dislikes'
      if (payload.isLike === false) {
        HTTP.delete(endpoint).then(response => {
          commit('toggleVerseCommentLike', {commentId: payload.commentId, isReply: payload.isReply, isLike: null})
          commit('isSaving', false)
        }).catch(e => {
          commit('isSaving', false)
        })
      } else {
        HTTP.post(endpoint).then(response => {
          commit('toggleVerseCommentLike', {commentId: payload.commentId, isReply: payload.isReply, isLike: false})
          commit('isSaving', false)
        }).catch(e => {
          commit('isSaving', false)
        })
      }
    }
  }
}



