<template>
  <v-layout row>
    <v-flex xs12>
      <form @submit.prevent="submit" >
        <div :class="{commentTextbox : !parentCommentId}">
          <v-text-field flat multi-line rows="1" single-line counter=300 auto-grow
           :label="labelText" v-model="commentText"
            append-icon="send" :append-icon-cb="submitVerseComment"></v-text-field>
          </div>
      </form>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  props: ['parentCommentId'],
  data () {
    return {
      commentText: ''
    }
  },
  computed: {
    labelText () {
      return this.parentCommentId ? 'Reply' : 'Comment'
    }
  },
  methods: {
    submitVerseComment () {
      console.log('in submit', this.parentCommentId)
      this.$store.dispatch('saveVerseComment', {commentText: this.commentText, parentCommentId: this.parentCommentId}).then(r => {
        this.commentText = null
      })
    }
  }
}
</script>

<style scoped>
    .commentTextbox {
        position:fixed;
        bottom:0%;
        width:95%;
        z-index:50;
        background-color:white;
    }
</style>



