<template>
  <div v-on:keyup.left="previousVerse" v-on:keyup.right="nextVerse" tabindex=-1>
    <v-container fluid class="mycontainer">
      <tatva-loader></tatva-loader>

      <verse-text></verse-text>
      <next-verse-button></next-verse-button>
      <!-- <tatva-comments-and-replies></tatva-comments-and-replies> -->
      <tatva-comments @openCommentReply="openCommentReplyDialog"></tatva-comments>
       <v-dialog v-model="isReply" fullscreen persistent max-width="290" style="margin:50px; padding:20px">
          <v-card>
            <v-toolbar card dark color="primary">
              <v-spacer></v-spacer>
              <v-btn icon @click.native="isReply = false" dark><v-icon>close</v-icon></v-btn>
            </v-toolbar>
            <div style="padding:10px">
              <tatva-comment-textbox :parentCommentId="parentCommentId"></tatva-comment-textbox>
            </div>
            <tatva-comments isReply="true"></tatva-comments>
          </v-card>
      </v-dialog>
      <tatva-comment-textbox></tatva-comment-textbox>
    </v-container>
  </div>
</template>

<script>
import tatvaLoader from './TatvaLoader'
import verseText from './VerseText'
import nextVerseButton from './NextVerseButton'
import tatvaCommentTextbox from './VerseComments/TatvaCommentTextbox'
import tatvaComments from './VerseComments/TatvaComments'

export default {
  components: {
    tatvaLoader,
    verseText,
    nextVerseButton,
    tatvaCommentTextbox,
    tatvaComments
  },
  data () {
    return {
      isReply: false,
      parentCommentId: null
    }
  },
  mounted () {
    var verseOfDayId = this.$route.query.id
    if (verseOfDayId) {
      this.$store.dispatch('changeIndexToVerseOfDay', {id: verseOfDayId})
    }
    this.$store.dispatch('fetchVerseComments')
  },

  computed: {
    currentVerseId () {
      return this.$store.getters.currentVerseId
    },
    isLoggedIn () {
      return this.$store.getters.isLoggedIn
    },
    verseCommentReplies () {
      return this.$store.getters.verseCommentReplies
    }
  },

  methods: {
    openCommentReplyDialog (parentCommentId) {
      console.log('event fired', parentCommentId)
      this.isReply = true
      this.parentCommentId = parentCommentId
      this.$store.dispatch('fetchVerseCommentReplies', parentCommentId)
    }
  }
}
</script>
<style scoped>
    .commentReplyTextbox {
      position:fixed;
      bottom:30%;
      width:95%;
      z-index:60;
      background-color:white;
    }

@media only screen and (max-width: 599px) {
    .mycontainer {
      padding: 5px !important;
    }
}
</style>



